-margin-bottom);\n\n              flex-grow: 1;\n\n              &--col-4 {\n                max-width: getProviderInSizes(provider-col-4-width);\n              }\n\n              &--col-6 {\n                max-width: getProviderInSizes(provider-col-6-width);\n              }\n            }\n          }\n        }\n      }\n\n      .ProviderButtonGroup {\n        @include flex(center, center);\n\n        margin-top: auto;\n\n        position: relative;\n        .ProviderCloseButton {\n          margin-right: getProviderInSizes(provider-button-closeMargin);\n        }\n        .ProviderBackButton {\n          @include flex(center, center);\n          border: 1px solid #8ea6c1;\n          padding: 7px;\n          position: absolute;\n          border-radius: 100%;\n          left: 0;\n          cursor: pointer;\n        }\n        .ProviderButtons {\n          @include flex(center, center);\n        }\n      }\n    }\n  }\n}\n"],"sourceRoot":""}]);
