/templates/main';\n\n.FiltersBase {\n  background-color: white;\n  box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.05);\n  width: 100%;\n  box-sizing: border-box;\n\n  .FiltersContainer {\n    display: flex;\n    align-items: flex-start;\n    flex-wrap: wrap;\n    box-sizing: border-box;\n    width: 100%;\n    margin: 0rem 1.6rem 0 1.6rem;\n    padding-top: 2.8rem;\n\n    .FilterContainer {\n      width: 23rem;\n      margin-right: 1.6rem;\n      margin-bottom: 0.2rem;\n      // margin-top: 2.4rem;\n\n      &.FilterContainerFromTo {\n        width: 47.5rem;\n        position: relative;\n        @include flex;\n      }\n\n      &:empty {\n        display: none;\n      }\n    }\n  }\n\n  &--closed {\n    height: 7rem;\n\n    .FiltersContainer {\n      display: none;\n    }\n\n    .Checkbox {\n      display: none;\n    }\n\n    .ArrowIconContainer {\n      transform: rotate(180deg);\n    }\n  }\n\n  .ControlContainer {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    padding: 1.6rem;\n    .FilterLabel {\n      font-weight: 500;\n      color: #505d6e;\n      letter-spacing: 0.14rem;\n      margin-right: 0.8rem;\n    }\n    .ToggleContainer {\n      display: flex;\n      align-items: center;\n      .ButtonToogle {\n        width: 131px;\n      }\n\n      .ClearLabelActive {\n        color: #3c54b2;\n        margin-right: 3.2rem;\n        cursor: pointer;\n      }\n      .ClearLabel {\n        color: #3c54b2;\n        opacity: 0.5;\n        margin-right: 3.2rem;\n        cursor: default;\n      }\n      .UserFoundLabel {\n        color: #505d6e;\n        letter-spacing: 0.14rem;\n        margin-right: 2.4rem;\n      }\n      .ArrowIconContainer {\n        width: 2.4rem;\n        height: 2.4rem;\n        margin-right: 2.4rem;\n        transition: transform 0.5s;\n        cursor: pointer;\n      }\n    }\n  }\n}\n\n.Checkbox {\n  display: flex;\n  margin: 2px 0 2px 16px;\n  flex-wrap: wrap;\n  &:empty {\n    display: none;\n  }\n}\n.DividerCheckbox {\n  width: 1214px;\n  margin: auto;\n  border: solid 1px #f9f9f9;\n  background-color: #f9f9f9;\n  margin-top: 26.5px;\n}\n"],"sourceRoot":""}]);
